/* Color Theme Swatches in HSLA */
body {
  font-family: "Rubik", sans-serif;
}

.root {
  display: grid;
  grid-template-columns: 1fr;
  gap: 5rem;
}
@media (min-width: 992px) {
  .root {
    grid-template-columns: 30rem 1fr;
    gap: 3rem;
  }
}

.sidebar {
  min-height: 100vh;
  position: fixed;
  background-color: #1AA3D9;
  padding: 3rem;
  color: #F2F2F2;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 3rem repeat(2, 1fr);
  gap: 4rem;
  width: 100%;
  max-width: 100vw;
}
@media (min-width: 992px) {
  .sidebar {
    max-width: 30rem;
  }
}

.sidebar__logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  grid-column: 1/4;
}

.sidebar__logo-name {
  font-size: 2.6rem;
  font-weight: 600;
  margin: 0;
}

.hamburger {
  grid-column: 4/5;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.hamburger__item {
  height: 0.5rem;
  width: 100%;
  background-color: #F2F2F2;
  display: block;
}
@media (min-width: 992px) {
  .hamburger__item {
    display: none;
  }
}

.menu, .options {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.menu {
  grid-row: 2/3;
  grid-column: 1/5;
  align-items: center;
}

.menu__item, .options__item {
  color: #F2F2F2;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-size: 2.4rem;
  position: relative;
  margin-left: 11rem;
  display: inline-block;
}
@media (min-width: 992px) {
  .menu__item, .options__item {
    margin-left: 0;
    transition: all 0.3s ease;
  }
}

.menu__item:hover, .options__item:hover {
  color: #fff;
  background-color: #F2A0AF;
  border-radius: 1rem;
  padding: 1rem;
}

.menu__item__text {
  position: absolute;
  margin-left: 1rem;
  top: 0;
}

.menu__item:hover .menu__item__text, .options__item:hover .option__item__text {
  top: unset;
}

.options {
  padding-top: 3rem;
  max-height: 20rem;
  grid-row: 3/4;
  grid-column: 1/5;
}

.panel {
  grid-column: 2/3;
}